.red{
  color: red;
}
.bold{
  font-weight: bold;
}
.HeadSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.HeadSection button{
  background-color: green;
  color: white;
  font: inherit;
  border: 1px solid blue;
  padding: 8px;
  cursor: pointer;
  transition: background-color linear 500ms;
}
.HeadSection button:hover {
  background-color: lightgreen;
  color:black;
}
.HeadSection button.red{
  background-color: red;
  border: 1px solid blue;
}
.HeadSection button.red:hover {
  background-color: salmon;
  color:black;
}